$second: 1s;
$minute: $second * 60;
$hour: $minute * 60;
$day: $hour * 12;
$c-main: rgb(177, 217, 136);

$clock-size: 500px;
$dot-size: 25px;

$width-sec: 5px;
$height-sec: $clock-size / 2.2;
$c-sec: #000;

$width-mint: 10px;
$height-mint: $clock-size / 2.5;
$c-mint: rgb(89, 99, 110);

$width-hour: 10px;
$height-hour: $clock-size / 3.5;
$c-hour: #234;
